<template>
	<view class="index">
		<canvas :canvas-id="canvasId" id="canvasPie" class="charts" @touchstart="touchPie"></canvas>
	</view>
</template>

<script>
	import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
	import { bus } from '@/utils/bus.js';
	export default {
		props: {
			canvasId: {
				type: String,
				default: "canvasColumn"
			},
			legendShow: {
				type: Boolean,
				default: false
			},
			Unit: {
				type: String,
				default: ""
			},
			cWidth: {
				type: Number,
				default: uni.upx2px(690)
			},
			cHeight: {
				type: Number,
				default: uni.upx2px(500)
			},
			Column: {
				type: [Object, Array],
				default () {
					return {
						series: []
					}
				}
			}
		},
		data() {
			return {
				canvaColumn: null
			}
		},
		created() {
			if (this.canvasId != 'canvasColumn') {
				this.showColumn(this.canvasId, this.Column);
			} else {
				bus.$on('changePie',(data)=>{
					this.showColumn(this.canvasId, data);
				})
			}
		},
		// watch: {
		// 	Column(newVal, oldVal) {
		// 		if (newVal) {
		// 			this.showColumn(this.canvasId, this.Column);
		// 		}
		// 	},
		// 	immediate: true
		// },
		// mounted() {
		// 	this.showColumn(this.canvasId, this.Column);
		// },
		methods: {
			showColumn(canvasId, chartData) {
				for (let i = 0; i < chartData.series.length; i++) {
					chartData.series[i].format = (val) => {
						return val + this.Unit
					}
					chartData.series[0]?chartData.series[0].color = '#03958C':'';
					chartData.series[1]?chartData.series[1].color = '#F5C824':'';
					chartData.series[2]?chartData.series[2].color = '#F5645A':'';
					chartData.series[3]?chartData.series[3].color = '#64b532':'';
				}
				
				this.canvaColumn = new uCharts({
					$this: this,
					canvasId: canvasId,
					type: 'pie',
					fontSize: 11,
					legend: {
						show: true,
						// position: 'right',
						float: 'center',
						itemGap: 10,
						padding: 5,
						lineHeight: 26,
						margin: 0,
						borderWidth: 5,
						borderColor: '#ffffff'
					},
					background: '#FFFFFF',
					pixelRatio: 1,
					series: chartData.series,
					animation: true,
					width: this.cWidth,
					height: this.cHeight,
					dataLabel: true,
					extra: {
						pie: {
							labelWidth: 15
						}
					},
				});
				// this.piearr = this.canvaColumn.opts.series;
			},
			toJSON(){},
			touchPie(e) {
				this.canvaColumn.showToolTip(e, {
					format: function(item) {
						return item.name + ':' + item.data
					},
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
	.index {
		width: 100%;

		.charts {
			width: 100%;
			height: 500rpx;
		}
	}
</style>
